<template>
  <div id="mainForm" class="fixed w-ext h-ext z-50">
    <!--cross-->
    <button
      @click="disable"
      class="absolute w-6 h-6 right-2 top-0 hover:opacity-40 transition-all z-50"
    >
      <span
        id="cross1"
        class="absolute w-4 border-b-gray-600 rotate-45 border-b-2 top-1/3 right-2 transition-all"
      ></span>
      <span
        id="cross2"
        class="absolute w-4 border-b-gray-600 -rotate-45 border-b-2 top-1/3 right-2 transition-all"
      ></span>
    </button>
    <!--form-->
    <div
      id="form"
      class="absolute w-ext h-ext flex justify-center rounded-xl border-2 border-gray-500 items-center z-40"
    >
      <!--dragger-->
      <div
        @mousedown="drag"
        @mousemove="dragging"
        @mouseup="drop"
        class="absolute -top-6 w-full h-12 z-50"
      ></div>
      <div
        class="absolute top-0 w-full h-4 opacity-50 bg-gray-600 rounded-t-lg z-30"
      ></div>
      <div
        id="background"
        class="absolute w-ext h-ext bg-black-ext rounded-xl backdrop-blur-2xl opacity-70 z-10"
      />
      <div
        class="absolute w-5/6 h-1/4 top-11 grid grid-cols-2 grid-rows-3 gap-0 top-5 z-40"
      >
        <p class="text-2xl text-white z-40 select-none">Time</p>
        <input
          v-model="time"
          class="h-8 outline-none bg-white text-lg indent-2 leading-3 rounded-md z-40 opacity-70 focus:opacity-100"
        />
        <p class="text-2xl text-white z-40 select-none">Conclusion</p>
        <select
          v-model="goodbye"
          class="h-8 outline-none border-none appearance-none bg-white text-md leading-3 rounded-md z-40 opacity-70 focus:opacity-100"
        >
          <option value="Yours sincerely,">Yours sincerely</option>
          <option value="Best wishes,">Best wishes</option>
          <option value="Thank you for your time,">
            Thank you for your time
          </option>
        </select>
        <p class="text-2xl text-white z-40 select-none">Name</p>
        <input
          v-model="urName"
          class="h-8 outline-none bg-white indent-2 text-lg leading-3 rounded-md z-40 opacity-70 focus:opacity-100"
        />
      </div>
      <div class="absolute w-5/6 top-44 z-40">
        <input
          id="time"
          :value="time + ','"
          class="relative w-full mb-2.5 h-inputTime bg-white rounded-md outline-none indent-2 text-lg leading-3 z-40 opacity-70 focus:opacity-100"
        />
        <textarea
          id="main"
          class="relative w-full h-inputMain bg-white rounded-md outline-none indent-2 text-lg leading-4 resize-none z-40 opacity-70 focus:opacity-100"
        />
      </div>
      <div class="absolute w-5/6 bottom-28 z-40">
        <input
          id="gb"
          :value="goodbye"
          class="absolute w-72 left-0 h-inputConc bg-white rounded-md outline-none indent-2 text-lg leading-3 z-40 opacity-70 focus:opacity-100"
        />
        <input
          id="urName"
          :value="urName"
          class="absolute w-72 right-0 h-inputConc bg-white rounded-md outline-none indent-2 text-lg leading-3 z-40 opacity-70 focus:opacity-100"
        />
      </div>
      <div class="absolute w-5/6 h-12 bottom-3 z-40">
        <button
          @click="copy"
          class="absolute w-36 h-12 bg-white opacity-80 rounded-lg hover:opacity-40 transition-all z-40"
        >
          Copy to clipboard
        </button>
        <button
          @click="save"
          class="absolute w-48 left-1/3 h-12 bg-white opacity-80 rounded-lg hover:opacity-40 transition-all z-40"
        >
          Save
        </button>
        <button
          @click="clean"
          class="absolute w-36 h-12 right-0 bg-white opacity-80 rounded-lg hover:opacity-40 transition-all z-40"
        >
          Clean all
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BetterLetterApp",
  data() {
    return {
      urName: "",
      time: "",
      goodbye: "",
      pickedUp: false,
      mainForm: "",
    };
  },
  components: {},
  created() {
    const dataInf = localStorage.getItem("information");
    let info = JSON.parse(dataInf);
    if (info === null) {
      this.urName = "";
      this.time = "";
      this.goodbye = "";
      return;
    }
    this.urName = info.UrName;
    this.time = info.Time.slice(0, -1);
    this.goodbye = info.Gb;
  },
  mounted() {
    this.mainForm = document.getElementById("mainForm");
  },
  methods: {
    drag() {
      this.mainForm.style.position = "absolute";
      this.pickedUp = true;
    },
    dragging() {
      if (this.pickedUp) {
        this.mainForm.style.left = event.clientX - 360 + "px";
        this.mainForm.style.top = event.clientY - 5 + "px";
      }
    },
    drop() {
      this.pickedUp = false;
    },
    disable() {
      let form = document.getElementById("form");
      let cross1 = document.getElementById("cross1");
      let cross2 = document.getElementById("cross2");
      form.classList.toggle("hidden");
      cross1.classList.toggle("rotate-0");
      cross2.classList.toggle("rotate-0");
      cross1.classList.toggle("border-8");
      cross2.classList.toggle("border-8");
      cross1.classList.toggle("opacity-100");
      cross2.classList.toggle("opacity-100");
    },
    copy() {
      let time = document.getElementById("time").value;
      let main = document.getElementById("main").value;
      let gb = document.getElementById("gb").value;
      let urName = document.getElementById("urName").value;
      let allInf = time + "\n" + main + "\n" + gb + "\n" + urName;
      navigator.clipboard.writeText(allInf);
    },
    clean() {
      let allinf = ["time", "main", "gb", "urName"];
      this.urName = "";
      this.time = "";
      this.goodbye = "";
      for (let i = 0; i < allinf.length; i++) {
        document.getElementById(`${allinf[i]}`).value = "";
      }
      localStorage.clear();
    },
    save() {
      let time = document.getElementById("time").value,
        main = document.getElementById("main").value,
        gb = document.getElementById("gb").value,
        urName = document.getElementById("urName").value;
      let infObj = {
        Time: time,
        Main: main,
        Gb: gb,
        UrName: urName,
      };
      localStorage.setItem("information", JSON.stringify(infObj));
    },
  },
};
</script>

